<template>
    <div id="root" class="container" style="padding-top: 15vh">
        <div class="row justify-content-center">
            <div class="col-3 flex-direction-column">
                <button class="btn btn-sm btn-primary mb-3" @click="modal = !modal" type="button">Open modal dialog</button>
                <button class="btn btn-sm btn-primary" @click="nonmodal = !nonmodal" type="button">Open non-modal dialog</button>
            </div>
        </div>

        <modal-dialog :visible.sync="modal"/>
        <nonmodal-dialog :visible.sync="nonmodal" :modal="false"/>
    </div>
</template>

<script>
    import ModalDialog from './component/modal-dialog.vue';
    import NonnodalDialog from './component/nonmodal-dialog.vue';

    export default {
        name: 'App',
        components: {
            'modal-dialog': ModalDialog,
            'nonmodal-dialog': NonnodalDialog
        },
        data() {
            return { modal: false, nonmodal: false };
        }
    }
</script>
