<template>
    <j-dialog v-bind="$attrs" v-on="$listeners">
        <template #title>介绍</template>
        <template #describe>Vue.js 是什么</template>

        <j-dialog-content>
            <p>
                Vue (读音 /vjuː/，类似于 view) 是一套用于构建用户界面的
                <strong>渐进式框架</strong>
                。与其它大型框架不同的是，Vue 被设计为可以自底向上逐层应用。Vue 的核心库只关注视图层，不仅易于上手，还便于与第三方库或既有项目整合。另一方面，当与
                <a href="https://cn.vuejs.org/v2/guide/single-file-components.html" class="text-success" target="_blank" rel="noopener">现代化的工具链</a>
                以及各种
                <a href="https://github.com/vuejs/awesome-vue#libraries--plugins" class="text-success" target="_blank" rel="noopener">支持类库</a>
                结合使用时，Vue 也完全能够为复杂的单页应用提供驱动。
            </p>

            <p>
                如果你想在深入学习 Vue 之前对它有更多了解，我们
                <a href="https://cn.vuejs.org/v2/guide/index#modal-player" class="text-success" target="_blank" rel="noopener">制作了一个视频</a>
                ，带您了解其核心概念和一个示例工程。
            </p>

            <p>
                如果你已经是有经验的前端开发者，想知道 Vue 与其它库/框架有哪些区别，请查看
                <a href="https://cn.vuejs.org/v2/guide/comparison.html" class="text-success" target="_blank" rel="noopener">对比其它框架</a>
                。
            </p>

            <h4>起步</h4>
            <div class="alert alert-secondary" role="alert">
                官方指南假设你已了解关于 HTML、CSS 和 JavaScript 的中级知识。如果你刚开始学习前端开发，将框架作为你的第一步可能不是最好的主意——掌握好基础知识再来吧！之前有其它框架的使用经验会有帮助，但这不是必需的。
            </div>
            <a href="https://cn.vuejs.org/v2/guide/installation" class="btn btn-info mb-3" type="button">安装</a>
            <p>
                尝试 Vue.js 最简单的方法是使用
                <a href="https://codesandbox.io/s/github/vuejs/vuejs.org/tree/master/src/v2/examples/vue-20-hello-world" class="text-success" target="_blank" rel="noopener">Hello World 例子</a>
                。你可以在浏览器新标签页中打开它，跟着例子学习一些基础用法。或者你也可以
                <a href="https://github.com/vuejs/vuejs.org/blob/master/src/v2/examples/vue-20-hello-world/index.html" class="text-success" target="_blank" rel="noopener">
                    创建一个
                    <code class="text-danger">.html</code>
                    文件
                </a>
                ，然后通过如下方式引入 Vue：
            </p>
            <p class="bg-light p-2">
                <code class="hljs text-body">
                    <span class="hljs-comment"><!-- 开发环境版本，包含了有帮助的命令行警告 --></span>
                    <span class="hljs-tag text-primary">
                        &lt;
                        <span class="hljs-name">script</span>
                        <span class="hljs-attr">src</span>
                        =
                        <span class="hljs-string text-success">"https://cdn.jsdelivr.net/npm/vue/dist/vue.js"</span>
                        &gt;
                    </span>
                    <span class="hljs-tag text-primary">
                        &lt;/
                        <span class="hljs-tag">script</span>
                        &gt;
                    </span>
                </code>
            </p>
        </j-dialog-content>

        <dismiss-footer/>
    </j-dialog>
</template>

<script>
    import DismissFooter from './dismiss-footer.vue';

    export default {
        name: 'ModalDialog',
        components: { 'dismiss-footer': DismissFooter }
    };
</script>
